<template>
  <auth-template title="Faça seu cadastro" :isBarber="isBarber">
    <template v-if="alertInfo">
      <alert icon="done">
        Obrigado por ter se cadastrado!
        <br />
        Verifique sua caixa de e-mail e confirme seu cadastro.
      </alert>

      <btn-secondary
        text="Voltar para o site"
        class="mt-12"
        @clicked="voltar"
        dark
      />
    </template>
    <template v-else>
      <form @submit.prevent="cadastrar">
        <div class="text-center mt-4">
          <btn-primary
            text="Cadastre-se com email"
            class="btn-cadastrar"
            @clicked="registerWithForm"
          />
        </div>

        <div class="text-center">
          <gbutton
            class="mt-6 mx-auto d-flex justify-center"
            :params="params"
            :renderParams="renderParams"
            :onSuccess="registerWithGoogle"
          >
            Cadastrar-se com o Google
          </gbutton>
        </div>

        <btn-secondary text="Voltar" class="mt-12" @clicked="voltar" dark />
      </form>
      <v-dialog
        v-model="formDialog"
        transition="dialog-bottom-transition"
        width="350"
        persistent
      >
        <v-card dark class="modal-content pa-4">
          <div class="text-right">
            <v-btn icon dark @click="fecharFormulario">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </div>
          <div>
            <div class="text-center">
              <img src="/logo.png" class="logo" />
            </div>

            <div class="form-group">
              <v-text-field
                id="name"
                label="Nome"
                v-model="name"
                :value="name"
                :rules="nameRules"
                :success="isNameValid(name)"
                :disabled="googleLogin"
                dark
                v-on:keyup.enter="focusToNext('email')"
                autocomplete="off"
                clearable
                required
                validate-on-blur
              >
                <template v-slot:prepend-inner>
                  <v-icon class="material-icons-outlined mr-2" dense
                    >person</v-icon
                  >
                </template>
              </v-text-field>
            </div>

            <div class="form-group">
              <v-text-field
                id="email"
                ref="email"
                v-model="email"
                :value="email"
                label="E-mail"
                autocomplete="off"
                :disabled="emailDisabled"
                :rules="emailRules"
                :success="isNameValid(email)"
                dark
                prepend-inner-icon="mail"
                v-on:keyup.enter="focusToNext('bornDate')"
                clearable
                required
                validate-on-blur
              >
                <template v-slot:prepend-inner>
                  <v-icon class="material-icons-outlined mr-2" dense
                    >mail</v-icon
                  >
                </template>
              </v-text-field>
            </div>

            <div class="form-group">
              <date-picker
                ref="bornDate"
                :id="bornDate"
                :value="bornDate"
                label="Data de Nascimento"
                :max="maxDate"
                @change="changeBornDate"
                v-on:keyup.enter="focusToNext('password')"
              />
            </div>

            <div class="form-group" v-show="!googleLogin">
              <v-text-field
                ref="password"
                :type="showPassword ? 'text' : 'password'"
                v-model="password"
                :rules="passwordRules"
                :success="isNameValid(password)"
                dark
                label="Senha"
                :append-icon="
                  showPassword ? 'visibility_off' : 'remove_red_eye'
                "
                @click:append="showPassword = !showPassword"
                v-on:keyup.enter="focusToNext('confirmPassword')"
                clearable
                required
                validate-on-blur
              >
                <template v-slot:prepend-inner>
                  <v-icon class="material-icons-outlined mr-2" dense
                    >vpn_key</v-icon
                  >
                </template>
              </v-text-field>
            </div>

            <div class="form-group" v-show="!googleLogin">
              <v-text-field
                ref="confirmPassword"
                :type="showConfirmPassword ? 'text' : 'password'"
                v-model="confirmPassword"
                :rules="passwordRules"
                :success="isNameValid(confirmPassword)"
                dark
                label="Confirmar Senha"
                :append-icon="
                  showConfirmPassword ? 'visibility_off' : 'remove_red_eye'
                "
                @click:append="showConfirmPassword = !showConfirmPassword"
                v-on:keyup.enter="enterClick"
                clearable
                required
                validate-on-blur
              >
                <template v-slot:prepend-inner>
                  <v-icon class="material-icons-outlined mr-2" dense
                    >vpn_key</v-icon
                  >
                </template>
              </v-text-field>
            </div>

            <div class="form-group">
              <div class="mt-2">
                <router-link
                  :to="{ name: 'site-terms' }"
                  class="primaryLight--text"
                  >Termos de uso</router-link
                >,
                <router-link
                  class="primaryLight--text"
                  :to="{ name: 'site-privacy-policy' }"
                  >Política de Privacidade</router-link
                >
              </div>
              <div class="mt-2">
                <v-checkbox
                  class="checkout"
                  v-model="aceptedTerm"
                  color="accent"
                  label="Li e concordo com os termos de uso da aplicação."
                  hide-details
                ></v-checkbox>
              </div>
            </div>

            <div class="text-center mt-6">
              <btn-primary
                class="btnRegister"
                text="CADASTRAR"
                :disabled="!isFormValid()"
                :loading="progressLoader"
                @clicked="register"
              />
            </div>
          </div>
          <input type="text" class="d-none" v-model="formToken" name="formToken" required />
        </v-card>
      </v-dialog>
    </template>
  </auth-template>
</template>

<script>
import Alert from '@/components/globais/Alert';
import gbutton from 'vue-google-login';
import AuthTemplate from './AuthTemplate';
import BtnPrimary from './BtnPrimary';
import BtnSecondary from './BtnSecondary';
import DatePicker from '@/components/globais/form/date-picker/DatePicker';
import DateMixin from '@/mixins/DateMixin';
import FormMixin from '@/mixins/FormMixin';
import NavigationMixin from '@/mixins/NavigationMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';
import ValidationMixin from '@/mixins/ValidationMixin';

export default {
  name: 'Register',
  components: {
    Alert,
    AuthTemplate,
    BtnPrimary,
    BtnSecondary,
    DatePicker,
    gbutton,
  },
  mixins: [
    DateMixin,
    FormMixin,
    NavigationMixin,
    SnackbarMixin,
    ValidationMixin,
  ],
  props: {
    alertInfo: { default: true },
    isBarber: { default: false },
    progressLoader: { default: false },
  },
  data: () => ({
    formDialog: false,
    googleLogin: false,
    googleId: null,
    showPassword: false,
    showConfirmPassword: false,
    name: '',
    email: '',
    formToken: '',
    password: '',
    confirmPassword: '',
    params: {
      client_id:
        '69364991955-nfb8lar5v22bfikoba0imp8vjmscr23h.apps.googleusercontent.com',
    },
    renderParams: {
      height: 45,
      width: 250,
      theme: 'dark',
    },
    aceptedTerm: null,
    bornDate: null,
    token: null,
  }),
  computed: {
    emailDisabled() {
      return this.token || this.googleLogin ? true : false;
    },
    maxDate() {
      return this.formatDateToDateEn(new Date());
    },
  },
  mounted() {
    this.bornDate = this.formatDateToDateEn(new Date());
    this.token = this.$route.query.token
      ? this.$route.query.token.replace(/ /g, '+')
      : null;
    if (this.token) {
      const jwt = this.parseJwt(this.token);
      this.email = jwt.usuario.barber_mail;
      this.openFormDialog();
    }
  },
  methods: {
    changeBornDate(value) {
      this.bornDate = value;
    },
    enterClick() {
      this.register();
    },
    fecharFormulario() {
      this.token = null;
      this.formDialog = false;
    },
    isFormValid() {
      if (!this.isNameValid(this.name)) return false;
      else if (!this.isEmailValid(this.email)) return false;
      if (!this.googleLogin) {
        if (!this.isPasswordValid(this.password)) return false;
        else if (!this.isPasswordValid(this.confirmPassword)) return false;
        else if (this.confirmPassword !== this.password) return false;
      } else if (!this.aceptedTerm) return false;
      else if (!this.bornDate) return false;
      return true;
    },
    register() {
      let date = new Date();
      date.setFullYear(date.getFullYear() - 18);
      if (date < new Date(this.parseDate(this.bornDate))) {
        this.showSnackbarErro(
          'O Meu Barbeiro só é permitido para maiores de idade!'
        );
      } else {
        if (this.isFormValid()) {
          if (this.googleLogin) {
            const data = {
              google_id: this.googleId,
              name: this.name,
              email: this.email,
              formToken: this.formToken,
              born_date: this.formatDateToDateEn(
                new Date(this.parseDate(this.bornDate))
              ),
              barber_token: this.token,
            };
            this.$emit('registerWithGoogle', data);
          } else {
            const data = {
              name: this.name,
              email: this.email,
              password: this.password,
              formToken: this.formToken,
              born_date: this.formatDateToDateEn(
                new Date(this.parseDate(this.bornDate))
              ),
              barber_token: this.token,
            };
            this.$emit('register', data);
          }
        } else this.showSnackbarErro('Preencha todos os campos corretamente!');
      }
    },
    openFormDialog() {
      this.formDialog = true;
      setTimeout(() => {
        document.querySelector('#name').focus();
      }, 500);
    },
    registerWithGoogle(googleUser) {
      const profile = googleUser.getBasicProfile();
      this.googleLogin = true;
      this.googleId = profile.getId();
      this.email = profile.getEmail();
      this.name = profile.getName();
      this.openFormDialog();
    },
    registerWithForm() {
      this.googleLogin = false;
      this.googleId = null;
      this.email = null;
      this.name = null;
      this.openFormDialog();
    },
    parseJwt(token) {
      let base64Url = token.split('.')[1];
      let base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      let jsonPayload = decodeURIComponent(
        atob(base64)
          .split('')
          .map(function(c) {
            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join('')
      );
      return JSON.parse(jsonPayload);
    },
    voltar() {
      if (this.isBarber) this.goTo('barber-auth');
      else this.goTo('login');
    },
  },
};
</script>

<style scoped>
.checkout {
  width: 100%;
}
.modal-content {
  background: #000000fa;
  padding: 24px !important;
  display: flex;
  justify-content: center;
  align-self: center;
  flex-direction: column;
  margin: auto;
}
.btnLogin {
  width: 180px;
}
.logo {
  width: 200px;
  max-width: 100%;
}
</style>
